@import '../../styles/init.scss';

.yux-join {
  width: 92%;
  font-size: rem(12);
  color: #fff;
  margin: rem(50) auto 2rem;

  h1 {
    font-size: rem(18);
    text-align: center;
    padding-top: rem(20);
  }

  h3 {
    text-align: center;
    font-size: rem(14);
    margin: rem(10) 0;
  }

  a {
    color: #3385FF;
    text-decoration: underline;
  }

  &-content {
    color: #ccc;
    line-height: 1.2rem;
  }
}